.header {
  max-width: 1920px;
  height: 88px;
  display: flex;
  justify-content: center;
  padding: 16px 0px;
  background: #1C1C21;
  box-sizing: border-box;
}

.container {
  min-width: 1280px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 1280px) {
  .container {
    min-width: 0;
    max-width: 1280px;
  }
}

.nav {
  display: flex;
}

.navLinkList {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
}

.navLinkContainer {
  display: flex;
  flex-wrap: wrap;
  text-decoration: none;
  color: white;
}

.logo {
  padding-right: 177px;
  padding-top: 3px;
}
